[data-theme-version="dark"] {
	.card {
		background-color: $dark-card;
		box-shadow: none;
	}

	.dropdown-menu {
		background-color: $dark-card;
		box-shadow: 0rem 0rem 0rem 0.0625rem rgba(255, 255, 255, 0.1);

		.dropdown-item {
			color: #777777;

			&.selected,
			&.selected.active,
			&.active,
			&:hover,
			&:focus {
				background-color: $dark-card;
				color: $white;
			}
		}
	}

	a {
		color: $white;
	}

	.btn-link g [fill] {
		fill: #fff;
	}

	.btn-light:active,
	.btn-light:focus,
	.btn-light:hover {
		color: #000;
	}

	.form-control {
		background-color: $d-bg;
		border-color: $d-border;
		color: $white;

	}

	.modal-content {
		background: $dark-card;
	}

	.modal-footer,
	.modal-header {
		border-color: $d-border;
	}

	.close {
		font-weight: 400;
		color: $white;
		text-shadow: none;

		&:hover {
			color: $white;
		}
	}

	.star-rating .product-review,
	.new-arrival-content .item {
		color: $white;
	}

	.custom-dropdown .dropdown-menu {
		border-color: $d-border;
	}

	.widget-stat .media>span {
		background: $d-border;
		border-color: $d-border;
		color: $white;
	}

	.plus-minus-input .custom-btn {
		background: $d-bg;
		border-color: $d-border;
	}

	.dropdown-divider,
	.size-filter ul li {
		border-color: $d-border;
	}

	.custom-select {
		border-color: $d-border;
		color: $d-ctl;
		background: $d-bg;
	}

	.nav-tabs {
		border-color: $d-border !important;
	}

	.mail-list .list-group-item.active i {
		color: $white;
	}

	hr {
		border-color: $d-border;
	}

	.grid-col {
		background: $d-bg;
	}

	.noUi-target {
		border-color: $d-border;
		border-radius: 0.5rem;
		box-shadow: none;

		.noUi-connects {
			background: $d-border;
		}
	}

	.noUi-marker-large,
	.noUi-marker {
		background: $d-border;
	}

	.input-group-text {
		background: $d-bg;
		color: $white;
		border-color: $d-border;
	}

	.note-editor.note-frame {
		border-color: $d-border;

		.btn {
			color: $white;
		}

		.note-editing-area .note-editable {
			color: $white;
		}
	}

	.widget-media .timeline .timeline-panel {
		border-color: $d-border;
	}

	.notification_dropdown .dropdown-menu-right .all-notification {
		border-color: $d-border;
	}

	#user-activity .nav-tabs .nav-link {
		border-color: $d-border;

		&.active {
			background: $d-bg;
			color: $white;
		}
	}

	.list-group-item-action {
		color: $body-color;
	}

	.list-group-item-action:focus,
	.list-group-item-action:hover,
	.list-group-item-action:focus {
		background-color: $d-bg;
		border-color: $d-bg;
	}

	.list-group-item.active {
		color: $white;
		border-color: var(--primary);

		&:focus,
		&:hover,
		&:focus {
			background-color: var(--primary);
			border-color: var(--primary);
			color: $white;
		}
	}

	.swal2-popup {
		background: $dark-card;
	}

	.form-head .btn-outline-primary {
		border-color: $d-border;
	}

	.form-head .btn-outline-primary:hover {
		border-color: var(--primary);
	}

	.review-tab.nav-pills li a.nav-link.active {
		background: transparent;
	}

	.new-arrival-content {
		h4 {
			a {
				color: $white;
			}
		}
	}

	.text-black {
		color: $white !important;
	}

	.abilities-chart .ct-chart .ct-label {
		fill: $white;
	}

	.morris_chart_height text tspan {
		fill: $white;
	}

	.btn-link {
		color: $white;

		svg {
			path {
				stroke: $white;
			}
		}
	}

	.order-bg {
		background: $d-bg;
	}

	.detault-daterange {
		background: $d-bg;
		color: $white;

		.input-group-text {
			background: $dark-card;
			border: 0;

		}
	}

	.dataTablesCard {
